import React from 'react';
import {
  Card, CardText, CardBody,
  CardTitle, CardSubtitle, CardImg
} from 'reactstrap';

const Card1 = (props) => {
  return (
    <div>
      <Card id="serverStats">
        <CardBody>
          <CardImg id="cardImg" src={props.image} />
          <CardTitle id="cardTitle">{props.title}</CardTitle>
          <CardSubtitle id="cardSub">{props.subTitle}</CardSubtitle>
          <CardText>{props.content}</CardText>
        </CardBody>
      </Card>
    </div>
  );
};

export default Card1;