import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavbarText,
  Button
} from 'reactstrap';

const Navbar1 = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div>
      <Navbar id="navbar1" color="dark" dark expand="md">
        <NavbarBrand id="navbarbrand1" href="/">{props.navbarBrand}</NavbarBrand>
        <NavbarToggler onClick={toggle} />
        <Collapse isOpen={isOpen} navbar>
          <Nav className="mr-auto" navbar>
          <NavbarText id="navbarbrand2">Demo</NavbarText>
          </Nav>
          <Button color="info" id="logbutton">Register</Button>
          <Button color="success" id="logbutton">Log in</Button>
        </Collapse>
      </Navbar>
    </div>
  );
}

export default Navbar1;